<template>
  <div>
    <h2>首页</h2>
     <go-back></go-back>
    <to-search></to-search>
    <div class="box">
    <h3>1.router-link</h3>
    <router-link to="/movie" v-if="type==='1'" >
      电影
    </router-link>
    <router-link to="/food" v-else >
      外卖
    </router-link>
    <p>router-link 不能做判断，点完就跳了</p>
   </div>
   <div class="box">
     <h3>2. $router.push('path')</h3>
     <button @click="toMovie()" v-if="type==='1'" >电影</button>
      <button @click="toFood()" v-else>外卖</button>
      <p>
        可以加判断，添加一条新的历史记录
      </p>
   </div>
   <div class="box">
      <h3>3. $router.replace('path')</h3>
      <button @click="toMovie1()" v-if="type==='1'">电影</button>
        <button @click="toFood1()" v-else >外卖</button>
   </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      type:localStorage.getItem('type')
    }
  },
  methods: {
    toMovie(){
      this.$router.push('/movie')
    },
    toFood(){
      this.$router.push('/food')
    },
    toMovie1(){
      this.$router.replace('/movie')
    },
    toFood1(){
      this.$router.replace('/food')
    }
  },

  }

</script>

<style>

</style>



